import React from 'react'
import PropTypes from 'prop-types'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
// import $ from 'jquery';
import BurgerMenu from 'react-burger-menu';
import { slide as Menu } from 'react-burger-menu'
import '../../bower_components/materialize/dist/css/materialize.min.css'
import './fonts/MyFontsWebfontsKit.css'
import './index.css'
import './font-awesome.css'
import Example from '../components/menu'

let divStyle = {
    backgroundColor:'white',
    boxShadow:'none',
    padding:'0 60px'
};

const Header = () => (
    <nav className="white" style={divStyle}>
        <div className="nav-wrapper">
            <a href="/" className="brand-logo turquoise-text-1 f-18 text-uppercase bold-text">Purpose Meets Profit</a>
            {/*<a href="#" data-activates="mobile-demo" className="button-collapse right"><i className="fa fa-bars turquoise-text-1" aria-hidden="true"/></a>*/}
            <ul className="right hide-on-med-and-down">
                <li><Link to="/giveaway/" className="turquoise-text-1 bold-text text-uppercase">Giveaway</Link></li>
                <li><a href="https://www.youtube.com/channel/UColDe80cdpOMHn3d7ecJxVg" target="_blank"><i className="fa fa-youtube-play turquoise-text-1" aria-hidden="true"/></a></li>
                <li><a href="https://twitter.com/Fownders?ref_src=twsrc%5Egoogle%7Ctwcamp%5Eserp%7Ctwgr%5Eauthor" target="_blank"><i className="fa fa-twitter turquoise-text-1" aria-hidden="true"/></a></li>
                <li><a href="https://www.facebook.com/fowndersnewark/" target="_blank"><i className="fa fa-facebook-square turquoise-text-1" aria-hidden="true"/></a></li>
                <li><a href="https://www.instagram.com/purposemeetsprofit/" target="_blank"><i className="fa fa-instagram turquoise-text-1" aria-hidden="true"/></a></li>
            </ul>
            <div className="hide-on-large-only">
                <Example/>
            </div>
        </div>
    </nav>

)

const TemplateWrapper = ({ children }) => (
    <div>
        <Helmet
            title="Purpose Meets Profit Podcast"
            meta={[
                { name: 'description', content: 'Welcome to the Purpose Meets Profit Podcast! Hosted by two millennial entrepreneurs, Logan Cohen and Gerard Adams, who let their purpose drive them while the profit keeps the lights on!' },
                { name: 'keywords', content: 'podcast, Fownders, Gerard Adams, Logan Coghen, millennial, entrepreneurs' },
                { name: 'image', content: '../static/Podcast-icon' },
                { name: 'og:title', content: 'Purpose Meets Profit Podcast' },
                { name: 'og:description', content: 'Welcome to the Purpose Meets Profit Podcast! Hosted by two millennial entrepreneurs, Logan Cohen and Gerard Adams, who let their purpose drive them while the profit keeps the lights on!' },
                { name: 'og:image', content: '../static/Podcast-icon' },
                { name: 'og:url', content: 'http://purposemeetsprofit.co/' },
                { name: 'og:site_name', content: 'Purpose Meets Profit Podcast' },
                { name: 'og:url', content: 'http://purposemeetsprofit.co/' },
                { name: 'twitter:image:src', content: '../static/Podcast-icon' },
                { name: 'og:type', content: 'website' },
                { name: 'og:image:width', content: '2017' },
                { name: 'og:image:height', content: '1350' },
                // TWITTER CARD
                { name: 'twitter:card', content: 'summary' },
                { name: 'twitter:title', content: 'Purpose Meets Profit Podcast' },
                { name: 'twitter:description', content: 'Welcome to the Purpose Meets Profit Podcast! Hosted by two millennial entrepreneurs, Logan Cohen and Gerard Adams, who let their purpose drive them while the profit keeps the lights on!' },
                { name: 'twitter:image:src', content: '../static/Podcast-icon' },
                { name: 'twitter:image:alt', content: 'Purpose Meets Profit' },
            ]}
        />
        <Header />
        <div
            style={{
                // margin: '0 auto',
                // maxWidth: 960,
                // padding: '0px 1.0875rem 1.45rem',
                // paddingTop: 0,
            }}
        >
            {children()}
        </div>
    </div>
)

TemplateWrapper.propTypes = {
    children: PropTypes.func,
    // componentDidMount(){
    //     $(document).ready(function(){
    //
    //          $(".button-collapse").sideNav();
    //     });
    // }
}



export default TemplateWrapper


